import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { AllModules, GridApi, GridOptions, IGetRowsParams } from '@ag-grid-enterprise/all-modules';
import { GridSizeChangedEvent } from 'ag-grid-community';
import { Router } from '@angular/router';
import { RenderEditDeleteComponent } from './render-edit-delete/render-edit-delete.component';
import { BranchService } from 'app/core/service/branch-bkpoint.service'
@Component({
  selector: 'app-store',
  templateUrl: './store.component.html',
  styleUrls: ['./store.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StoreComponent implements OnInit {

  defaultColDef;
  gridOptions;
  modules = AllModules;
  gridApi: GridApi;
  page: number = 1;
  rowdata ;

  constructor(
    private router: Router,
    private branchService: BranchService
  ) {
    this.gridOptions = {
      rowSelection: 'single',
      enableFilter:true,
      rowStyle: {'padding': '10px 0px'},
      rowHeight : 50.5,
      context: {
        componentParent: this
      }
    };
    this.defaultColDef = {
      resizable: true,
      sortable: false,
      suppressMenu: true,
      floatingFilterComponentParams: {suppressFilterButton:true},
      filter: 'agTextColumnFilter'
    };
   }

   columnDefs = [
    { headerName: "Branch ID", field: "branchId" , width: 200 },
    { headerName: "Branch Name", field: "branchName", width: 200},
    { headerName: "Manager Name", field: "managerName", width: 200 },
    { headerName: "Phone Number", field: "phoneNumber", width: 200 },
    { headerName: "Date Join", field: "dateJoin", width: 200, filterParams: { filterOptions: ['inRange'] }},
    { headerName: "Active", field: "isActive", cellRenderer: isActiveRenderer, width: 120, cellStyle: { textAlign: "center" }, sortable :false ,filterParams: { filterOptions: ['inRange']} },
    { headerName: "Edit/Delete", field: "edit", width: 200, filterParams: { filterOptions: ['inRange'] }, "cellRendererFramework": RenderEditDeleteComponent}
  ];

  ngOnInit(): void {
    this.getListBranch();
  }

  getListBranch(){
    this.branchService.getBranchList().subscribe(data=>{
      this.rowdata = data
    })
  }

  onRowClickedChangeToAllStore(event) {
    this.router.navigate(['bk-point/stores/store-of-branch'], { queryParams: { uuid: event.data.uuid } });
  };

  onGridSizeChanged(params: GridSizeChangedEvent)
  {
    params.api.sizeColumnsToFit();
  }

  onGridReady(params){
    params.api.sizeColumnsToFit();
    this.gridApi = params.api
    // this.getData(params.api);

  }

  getData(even){

  }

  gotoCreateStore(){
    this.router.navigate(['bk-point/stores/create']);
  }

}

function isActiveRenderer(params)
{
  let imgSource: string;
  const data = params.value;
  if (data === true){
    imgSource = `<img src="assets/icons/doxa-icons/ic_check_circle_24px.svg" vertical-align:'middle'/>`
  }else{
    imgSource = `<img src="assets/icons/doxa-icons/none.svg" />`
  }
  return imgSource;
}
