import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { BranchService } from 'app/core/service/branch-bkpoint.service';
import { CommonDialogComponent } from 'app/main/_shared/common-dialog/common-dialog.component';
import { ConfirmDialogComponent } from 'app/main/_shared/confirm-dialog/confirm-dialog.component';

@Component({
  selector: 'app-render-edit-delete',
  templateUrl: './render-edit-delete.component.html',
  styleUrls: ['./render-edit-delete.component.scss']
})
export class RenderEditDeleteComponent implements OnInit {

  gridApi: any;
  active: any;
  params: any;
  id: any;

  rowNode: any;

  constructor(
    private router: Router,
    private dialog: MatDialog,
    private branchService: BranchService
  ) { }

  agInit(params) {
    this.gridApi = params.api;
    this.params = params;
    this.id = params.data.id;
    this.active = params.value;
    this.rowNode = this.gridApi.getRowNode(this.params.node.id);
  }

  ngOnInit(): void {
    // console.log(this.params.context.componentParent)
  }

  onDelete(){
    // console.log(this.rowNode)
    const dialogRef = this.dialog.open(ConfirmDialogComponent, {
      width: '600px',
      data: { message: 'Are you sure you want to delete this branch ?', type: "REJECTED" }
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result === true) {
        this.branchService.deleteBranch(this.rowNode.data.uuid).subscribe(data=>{
          if(data.code === 200){
            const dialogNotifi = this.dialog.open(CommonDialogComponent, {
              width: "600px",
              data: {
                message:
                  'Delete branch successfully.',
                title:
                  "NOTIFICATION",
                colorButton: false
              },
            });
            dialogNotifi.afterClosed().subscribe(data => {
              this.params.context.componentParent.getListBranch();
            });
          }
        })
      } else {
        dialogRef.close();
      }
    });
  }

  onEdit(){
    this.router.navigate(['bk-point/stores/detail'], { queryParams: { uuid: this.rowNode.data.uuid } });
  }

}
