import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { AllModules, GridApi, IGetRowsParams } from '@ag-grid-enterprise/all-modules';
import { GridSizeChangedEvent } from 'ag-grid-community';
import { CustomerTable } from 'app/core/models/customer-bkpoint.model'
import { CustomerService } from 'app/core/service/customer-bkpoint.service'
@Component({
  selector: 'app-customer',
  templateUrl: './customer.component.html',
  styleUrls: ['./customer.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class CustomerComponent implements OnInit {

  defaultColDef;
  gridOptions;
  modules = AllModules;
  gridApi: GridApi;
  page: number = 1;
  rowdata ;

  constructor(
    private router: Router,
    private customerService : CustomerService
  ) {
    this.gridOptions = {
      rowSelection: 'single',
      // cacheBlockSize: 10,
      // maxBlocksInCache: 2,
      // enableServerSideFilter: true,
      enableFilter:true,
      // enableServerSideSorting: true,
      // rowModelType: 'infinite',
      // pagination: true,
      // paginationAutoPageSize: false,
      // paginationPageSize: 10,
      // sortingOrder: ["asc", "desc", null],
      rowStyle: {'padding': '10px 0px'},
      rowHeight : 50.5

    };
    this.defaultColDef = {
      resizable: true,
      sortable: false,
      suppressMenu: true,
      floatingFilterComponentParams: {suppressFilterButton:true},
      filter: 'agTextColumnFilter'
    };
   }

  columnDefs = [
    { headerName: "Customer ID", field: "id" , width: 200 },
    { headerName: "Customer Name", field: "name", width: 200},
    { headerName: "Location", field: "location", width: 200 },
    { headerName: "Email", field: "email", width: 200 },
    { headerName: "Phone Number", field: "phoneNumber", width: 200 },
    // { headerName: "Customer Of", field: "customerOf", width: 200, filterParams: { filterOptions: ['inRange'] }},
  ];

  ngOnInit(): void {
    this.customerService.getCustomerList().subscribe(data=>{
      this.rowdata = data;
    })
  }

  onRowClickedChangeToCustomer(event) {
    this.router.navigate(['bk-point/customers/detail'], { queryParams: { uuid: event.data.uuid } });
  };

  onGridSizeChanged(params: GridSizeChangedEvent)
  {
    params.api.sizeColumnsToFit();
  }

  onGridReady(params){
    params.api.sizeColumnsToFit();
    this.gridApi = params.api
    this.getData(params.api);

  }

  getData(even){

  }

}
