import { Injectable } from "@angular/core";
import { ApiService } from "./api.service";
import { HttpClient, HttpHeaders, HttpParams } from "@angular/common/http";
import { GetAdminEmailAndImageApi, updateImageProfile, GetAdminDetailApi, updateDbImage,
  changePassword, changeName} from "./backend-api";
import { Observable, throwError } from "rxjs";
import { catchError, map, retry } from "rxjs/operators";
import { isNullOrUndefined } from "util";
import { formatDate } from "@angular/common";
import { ImageAndEmail, Profile } from "../models/profile.model";
import * as _ from 'lodash';

@Injectable({
  providedIn: "root",
})
export class ProfileService {
  constructor(private api: ApiService, private http: HttpClient) {}

  getProfileEmailAndImage(): Observable<any> {
    return this.api.get(GetAdminEmailAndImageApi).pipe(
      map((data) => {
        if (data.code === 200 && data.data !== null && data.data !== undefined) {
          let profile = new ImageAndEmail();
          profile.email = data.data.email;
          profile.image = data.data.profile_photo_key;
          return profile;
        }
      })
    );
  }

  getProfileDetail(): Observable<any> {
    return this.api.get(GetAdminDetailApi).pipe(
      map((data) => {
        if (data.code === 200 && data.data !== null && data.data !== undefined) {
          let profile = new Profile();
          profile.email = data.data.email;
          profile.profilePhotoKey = data.data.profile_photo_key;
          profile.company = "BKPOINT";
          profile.role = "Admin";
          profile.name = data.data.last_name + " " + data.data.first_name;
          return profile;
        }
      })
    );
  }

  updateImageProfile(fileName: string, fileType: string): Observable<any> {
    var formImage = { name: fileName, type: fileType };
    return this.api.post(updateImageProfile, formImage);
  }

  uploadProfileImage(url: string, contentType: string, file) {
    const headers = new HttpHeaders({ "Content-Type": contentType });
    return this.http.put<any>(url, file, {
      headers: headers,
      reportProgress: true,
    });
  }

  changePassword(formPassword): Observable<any> {
    return this.api.put(changePassword, formPassword);
  }

  changeName(formName): Observable<any> {
    return this.api.put(GetAdminEmailAndImageApi, formName);
  }
  updateDBImage(profile_image : string)
  {
    let param = new HttpParams();
    param = param.append('profile_image', profile_image)
    return this.http.put(updateDbImage,'',{headers: this.api.headers, params: param}).pipe(
      map((value) =>
      {
      }), catchError(value => throwError(value))
    );
  }
}
