import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common'
import { CommonDialogComponent } from 'app/main/_shared/common-dialog/common-dialog.component';
import { MatDialog } from '@angular/material/dialog';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { ConfirmDialogComponent } from 'app/main/_shared/confirm-dialog/confirm-dialog.component';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { BranchService } from 'app/core/service/branch-bkpoint.service';
import * as CITY from 'assets/city.json';

@Component({
  selector: 'app-store-detail',
  templateUrl: './store-detail.component.html',
  styleUrls: ['./store-detail.component.scss']
})
export class StoreDetailComponent implements OnInit {

    // Upload File
    public imagePath : string;
    imgURL: any;
    imageArray: string[] = [];
    files: any[] = [];
    submitted;
    // ----------------
    historyUpdate= [];

    //Form---------
    branchForm: FormGroup;

    //Address
    selectedCity;
    city;

    uuid : string;
    is_active: boolean = false;

    //Get data from serve
    firstName: string;
    lastName: string;
    branchName: string = "Quoc Sang";
    branchDescription: string;
    emailManager: string;
    phoneNumber: string

  constructor(
    private location : Location,
    public dialog: MatDialog,
    private router: Router,
    private formBuilder: FormBuilder,
    private branchService: BranchService,
    private activatedRoute: ActivatedRoute,
  ) { }

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe((params) => {
      this.uuid = params.uuid;
    });
    this.city = (CITY as any).default;
    this.branchForm = this.formBuilder.group({
      branch_name:['', Validators.required],
      branch_description: ['', Validators.required],
      first_name : ['', Validators.required],
      last_name : [],
      phone_number: ['', Validators.required],
      email: ['', Validators.required],
      // address_detail: ['', Validators.required],
      // district: ['', Validators.required]
    });

    this.getDataFromServe(this.uuid)

  }

  getDataFromServe(uuid){
    this.branchService.getBranchByUuid(uuid).subscribe(data=>{
      if(data !== null && data !== undefined){
        this.is_active = data.is_active;
        this.branchName = data.brand_name;
        this.branchDescription = data.brand_description;
        if(data.managerBy !== null && data.managerBy !== undefined){
          this.firstName = data.managerBy.first_name;
          this.lastName = data.managerBy.last_name;
          this.phoneNumber = data.managerBy.phone_number;
          this.emailManager = data.managerBy.email;
        }
      }
    })
  }

  get f() { return this.branchForm.controls };

  notCreateBranch(){
    const dialogRef = this.dialog.open(ConfirmDialogComponent, {
      width: '600px',
      data: { message: 'Your changes will not be saved, do you wish to leave?', type: "REJECTED" }
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result === true) {
        this.router.navigate(['bk-point/stores']);
      } else {
        dialogRef.close();
      }
    });
  }

  updateBranch(){
    let formUpdateBranch = {
      branch_name: this.branchForm.value.branch_name ,
      branch_description : this.branchForm.value.branch_description,
      phone_number : this.branchForm.value.phone_number,
      email: this.branchForm.value.email,
      first_name : this.branchForm.value.first_name,
      last_name : this.branchForm.value.last_name,
      is_active : this.is_active
    }
    // console.log(formUpdateBranch)
    this.branchService.updateBranch(this.uuid , formUpdateBranch).subscribe(data=>{
      // console.log(data);
      if(data.code == 200){
        const dialogNotifi = this.dialog.open(CommonDialogComponent, {
          width: "600px",
          data: {
            message:
              data.message ,
            title:
              "NOTIFICATION",
            colorButton: false
          },
        });
        dialogNotifi.afterClosed().subscribe(data => {
          this.router.navigate(['bk-point/stores']);
        });
      }
    })
  }

  onFileDropped(event){
    this.preview(event);
    this.prepareFilesList(event);
  }

  preview(files) {
    let reader = new FileReader();
    this.imagePath = files;
    let file = files[0];
    reader.readAsDataURL(files[0]);
    reader.onload = (_event) => {
      let preSignedUrl: string;
      let profilePhotoKey: string;
      // this._productService.getPreSignedUrl(Date.now().toString() + file.name, file.type).subscribe(response => {
      //   if (response.code === 200) {
      //     profilePhotoKey = response.key;
      //     preSignedUrl = response.url;
      //     this._productService.uploadActivityImage(preSignedUrl, file.type, file).subscribe(data => {
      //       this.imageArray.push(this.storageUrl + profilePhotoKey);
      //     });
      //   }
      // });

      this.imgURL = reader.result;
      return this.imageArray;
    };
  }

  prepareFilesList(files: Array<any>) {
    for (const item of files) {
      item.progress = 0;
      this.files.push(item);
    }
    this.uploadFilesSimulator(0);
  }

    /**
   * Simulate the upload process
   */
  uploadFilesSimulator(index: number) {
    setTimeout(() => {
      if (index === this.files.length) {
        return;
      } else {
        const progressInterval = setInterval(() => {
          if (this.files[index].progress === 100) {
            clearInterval(progressInterval);
            this.uploadFilesSimulator(index + 1);
          } else {
            this.files[index].progress += 10;
          }
        }, 200);
      }
    }, 1000);
  }

    /**
  * handle file from browsing
  */
 fileBrowseHandler(files) {
  if (files.length === 0)
    return;
  if (this.imageArray.length >= 6) {
    const dialogNotifi = this.dialog.open(CommonDialogComponent, {
      width: "500px",
      data: {
        message:
          'Maximum pictures  is 6',
        title:
          "NOTIFICATION",
        colorButton: false
      },
    });
    dialogNotifi.afterClosed().subscribe(data => {

    });
    return false;
  }
  if (files[0].size > 4 * 1024 * 1024) {
    // console.log(files[0].size);
    const dialogNotifi = this.dialog.open(CommonDialogComponent, {
      width: "500px",
      data: {
        message:
          'Maximum File Size 4.00 MB. Please choose another picture',
        title:
          "NOTIFICATION",
        colorButton: false
      },
    });
    dialogNotifi.afterClosed().subscribe(data => {

    });
    return false;
  }
  this.preview(files);
  this.prepareFilesList(files);
  }

    /**
   * Delete file from files list
   * @param index (File index)
   */
  deleteFile(index: number) {
    const dialogRef = this.dialog.open(FuseConfirmDialogComponent, {
      width: '500px',
      data: { message: 'Do you wish to do delete this image?'}
    });
    dialogRef.afterClosed().subscribe(result =>
    {
      if (result === true)
      {
        this.files.splice(index, 1);
        this.imageArray.splice(index, 1)
      } else
      {
        dialogRef.close();
      }
    })
  }

  formatBytes(bytes, decimals) {
    if (bytes === 0) {
      return '0 Bytes';
    }
    const k = 1024;
    const dm = decimals <= 0 ? 0 : decimals || 2;
    const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
  }

  drop(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.imageArray, event.previousIndex, event.currentIndex);
  }

  back(){
    this.location.back();
  }

  checkSelectedDistrict(event){
    console.log(event)
  }

}
