import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from "@angular/common";
import { CustomerDetail } from 'app/core/models/customer-bkpoint.model'
import { CustomerService } from 'app/core/service/customer-bkpoint.service';
import { GridSizeChangedEvent } from '@ag-grid-community/all-modules';
import { CheckNullOrUndefinedOrEmpty } from 'app/core/utils/common-function';
@Component({
  selector: 'app-customer-detail',
  templateUrl: './customer-detail.component.html',
  styleUrls: ['./customer-detail.component.scss'],
})
export class CustomerDetailComponent implements OnInit {

  constructor(
    private location: Location,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private customerService: CustomerService
  ) { }
  rowData;
  uuid: string;
  customerInformation: CustomerDetail;
  customerUpdateHistory: any = [];
  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe((params) => {
      this.uuid = params.uuid;
    });

    this.customerService.getCustomerByUuid(this.uuid).subscribe(data=>{
      if(!CheckNullOrUndefinedOrEmpty(data)){
        // console.log(data)
        this.customerInformation = data
        this.rowData = this.customerInformation.customerPoint
      }

    })
  }

  columnDefs = [
    { headerName: "Branch ID", field: "id", width: 100  },
    { headerName: "Branch Name", field: "name",width: 150 },
    // { headerName: "Location", field: "location",width: 250, filter: 'agTextColumnFilter' },
    // { headerName: "Phone Number", field: "phoneNumber", width: 150,filter: 'agTextColumnFilter' },
    { headerName: "Point", field: "point",  width: 150 },
  ];

  back() {
    this.location.back();
  }

  gotoStoreDetail(){
    this.router.navigate(['bk-point/stores/detail']);
  }

  onGridSizeChanged(params: GridSizeChangedEvent)
  {
    params.api.sizeColumnsToFit();
  }

}
