import { AllModules, GridApi, GridSizeChangedEvent } from '@ag-grid-enterprise/all-modules';
import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { VoucherService } from 'app/core/service/voucher.service';

@Component({
  selector: 'app-voucher',
  templateUrl: './voucher.component.html',
  styleUrls: ['./voucher.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class VoucherComponent implements OnInit {

  constructor(
    private router: Router,
    private voucherService : VoucherService
  ) {
    this.gridOptions = {
      rowSelection: 'single',
      // cacheBlockSize: 10,
      // maxBlocksInCache: 2,
      // enableServerSideFilter: true,
      enableFilter:true,
      // enableServerSideSorting: true,
      // rowModelType: 'infinite',
      // pagination: true,
      // paginationAutoPageSize: false,
      // paginationPageSize: 10,
      // sortingOrder: ["asc", "desc", null],
      rowStyle: {'padding': '10px 0px'},
      rowHeight : 50.5

    };
    this.defaultColDef = {
      resizable: true,
      sortable: false,
      suppressMenu: true,
      floatingFilterComponentParams: {suppressFilterButton:true},
      filter: 'agTextColumnFilter'
    };
  }

  defaultColDef;
  gridOptions;
  modules = AllModules;
  gridApi: GridApi;
  page: number = 1;
  rowdata ;

  ngOnInit(): void {
    // this.rowdata = [
    //   {voucher: "A'dor",serialNumber: '1234', type: "BKPOINT", started :"22-11-2020", expired: "25-11-2020"  }
    // ]
    this.voucherService.getListVoucher().subscribe(data=>{
      this.rowdata = data
    })
  }

  columnDefs = [
    { headerName: "Voucher", field: "voucherName" , width: 200,  },
    { headerName: "Description", field: "voucherDescription", width: 200},
    { headerName: "Voucher Type", field: "type", width: 200 },
    { headerName: "Voucher Started", field: "started", width: 200 , filterParams: { filterOptions: ['inRange'] } },
    { headerName: "Voucher Expired", field: "expired", width: 200 , filterParams: { filterOptions: ['inRange'] } },
  ];


  onRowDoubleClicked(event) {
    // this.router.navigate(['bk-point/customers/detail'], { queryParams: { uuid: event.data.uuid } });
  };

  onGridSizeChanged(params: GridSizeChangedEvent)
  {
    params.api.sizeColumnsToFit();
  }

  onGridReady(params){
    params.api.sizeColumnsToFit();
    this.gridApi = params.api
  }

  gotoCreateVoucher(){
    this.router.navigate(['bk-point/voucher/create']);
  }

}
