import { AllModules, GridApi } from '@ag-grid-enterprise/all-modules';
import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { GridSizeChangedEvent } from 'ag-grid-community';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common'
import { BranchService } from 'app/core/service/branch-bkpoint.service';
@Component({
  selector: 'app-store-of-branch',
  templateUrl: './store-of-branch.component.html',
  styleUrls: ['./store-of-branch.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StoreOfBranchComponent implements OnInit {

  constructor(
    private router: Router,
    private location: Location,
    private branchService: BranchService,
    private activatedRoute: ActivatedRoute,
  ) {
    this.gridOptions = {
      rowSelection: 'single',
      enableFilter:true,
      rowStyle: {'padding': '10px 0px'},
      rowHeight : 50.5

    };
    this.defaultColDef = {
      resizable: true,
      sortable: false,
      suppressMenu: true,
      floatingFilterComponentParams: {suppressFilterButton:true},
      filter: 'agTextColumnFilter'
    };
   }

  branchName: string = 'Test';
  defaultColDef;
  gridOptions;
  modules = AllModules;
  gridApi: GridApi;
  page: number = 1;
  rowdata ;
  uuid: string;

  columnDefs = [
    { headerName: "Store ID", field: "storeId" , width: 100 },
    { headerName: "Store Name", field: "storeName", width: 200},
    // { headerName: "Store Manager Name", field: "storeManagerName", width: 200 },
    // { headerName: "Phone Number", field: "phoneNumber", width: 200 },
    { headerName: "Address", field: "address", width: 200 },
    { headerName: "Date Join", field: "dateJoin", width: 200, filterParams: { filterOptions: ['inRange'] }},
    { headerName: "Active", field: "isActive", cellRenderer: isActiveRenderer, width: 120, cellStyle: { textAlign: "center" }, sortable :false ,filterParams: { filterOptions: ['inRange']} }

  ];

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      this.uuid = params.uuid;
    });
    this.branchService.getStoreOfBranchByUuid(this.uuid).subscribe(data=>{
      // console.log(data)
      this.branchName = data.branchName;
      this.rowdata = data.listStoreTable;
    })
  }

  onGridSizeChanged(params: GridSizeChangedEvent)
  {
    params.api.sizeColumnsToFit();
  }

  onGridReady(params){
    params.api.sizeColumnsToFit();
    this.gridApi = params.api

  }

  onRowClickedChangeToStoreDetail(event){

  }

  back(){
    this.location.back();
  }

}

function isActiveRenderer(params)
{
  let imgSource: string;
  const data = params.value;
  if (data === true){
    imgSource = `<img src="assets/icons/doxa-icons/ic_check_circle_24px.svg" vertical-align:'middle'/>`
  }else{
    imgSource = `<img src="assets/icons/doxa-icons/none.svg" />`
  }
  return imgSource;
}
