import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { Location } from '@angular/common'
import { ConfirmDialogComponent } from 'app/main/_shared/confirm-dialog/confirm-dialog.component';
import { BranchService } from 'app/core/service/branch-bkpoint.service';
import { CreateVoucher } from 'app/core/models/voucher-bkpoint.model';
import { VoucherService } from 'app/core/service/voucher.service';

@Component({
  selector: 'app-create-voucher',
  templateUrl: './create-voucher.component.html',
  styleUrls: ['./create-voucher.component.scss']
})
export class CreateVoucherComponent implements OnInit {

  constructor(
    private location : Location,
    public dialog: MatDialog,
    private router: Router,
    private formBuilder: FormBuilder,
    private branchService: BranchService,
    private voucherService: VoucherService
  ) { }

  voucherStartTimeData : Date;
  voucherEndTimeData : Date;
  minDateStart: Date = new Date();
  minDateEnd: Date = new Date();
  disable: boolean = true;

  listBranch;
  selectedBranch;

  submitted: boolean = false;
  number_discount: number ;
  min_order_value: number ;
  max_value_discount: number ;
  //Form---------
  voucherForm: FormGroup;
  ngOnInit(): void {
    this.branchService.getBranchListMultiselect().subscribe(data=>{
      this.listBranch = data
    })

    this.voucherForm = this.formBuilder.group({
      voucher_name:['', Validators.required],
      voucher_description: ['', Validators.required],
      start_time : ['', Validators.required],
      end_time : ['', Validators.required],
      point: ['', Validators.required],
    });
  }

  get f() { return this.voucherForm.controls };

  back(){
    this.location.back();
  }

  createVoucher(){
    const dialogRef = this.dialog.open(ConfirmDialogComponent, {
      width: '600px',
      data: { message: 'Are you wish to create new voucher?', type: "APPROVED" }
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result === true) {
        // this.router.navigate(['bk-point/voucher']);
        this.submitted = true;
        let formVoucher = new CreateVoucher();
        formVoucher.voucher_name = this.voucherForm.value.voucher_name;
        formVoucher.description = this.voucherForm.value.voucher_description;
        formVoucher.start_time = this.voucherStartTimeData;
        formVoucher.end_time = this.voucherEndTimeData;
        formVoucher.point_to_redeem =  this.voucherForm.value.point;
        formVoucher.min_order_value = this.min_order_value;
        formVoucher.max_redeem_value = this.max_value_discount;
        formVoucher.percent_discount = this.number_discount;
        formVoucher.is_active = true;
        console.log(formVoucher)
        this.voucherService.createVoucher(formVoucher).subscribe(data=>{
          // console.log(data)
          if(data.code===200){
            this.router.navigate(['bk-point/voucher']);
          }
        })
      } else {
        dialogRef.close();
      }
    });
  }

  notCreateaVoucher(){
    const dialogRef = this.dialog.open(ConfirmDialogComponent, {
      width: '600px',
      data: { message: 'Your changes will not be saved, do you wish to leave?', type: "REJECTED" }
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result === true) {
        this.router.navigate(['bk-point/voucher']);
      } else {
        dialogRef.close();
      }
    });
  }

  onChangeStart(event){
    this.minDateEnd = event;
    this.disable = false;
  }
}
