import { Injectable } from '@angular/core';
import { ApiService } from './api.service';
import { Observable, throwError } from 'rxjs';
import { catchError, map, retry } from 'rxjs/operators';
import { Voucher

  } from './backend-api';
import { HttpClient, HttpHeaders, HttpErrorResponse, HttpParams } from '@angular/common/http';
import { AllBranchTable , StoreOfBranchTable} from 'app/core/models/branch-manager.model'
import { formatDate } from '@angular/common';
import { data } from 'jquery';
import { CheckNullOrUndefinedOrEmpty } from '../utils/common-function';
import { voucherTable } from '../models/voucher-bkpoint.model';


@Injectable({
    providedIn: "root",
  })
  export class VoucherService {
    constructor(
        private api: ApiService,
        private http: HttpClient,
        ) {}

    createVoucher(formVoucher){
      return this.api.post(Voucher,formVoucher)
    }

    getListVoucher(){
      return this.api.get(Voucher).pipe(
        map(data=>{
          if(data.code === 200){
            return this.renderDataVoucherTable(data.data)
          }
        })
      )
    }

    renderDataVoucherTable(data){
      let listVoucher = []
      if(!CheckNullOrUndefinedOrEmpty(data)){
        data.forEach(element => {
          let voucher = new voucherTable();
          voucher.voucherName = element.voucher_name;
          voucher.voucherDescription = element.description;
          voucher.started = element.start_time;
          voucher.expired = element.end_time;
          voucher.type = (element.created_by_brand == 0)? "BKPOINT" : "BRAND";
          listVoucher.push(voucher)
        });
      }
      return listVoucher
    }
}
