import { Injectable } from '@angular/core';
import { ApiService } from './api.service';
import { Observable, throwError } from 'rxjs';
import { catchError, map, retry } from 'rxjs/operators';
import { GetAllBranchApi , CreateBranchApi ,GetBranchByUuid,
    UpdateBranchByUuid , CheckBranchAdminEmailExists , GetListBranch, GetPresignUrlApi
  } from './backend-api';
import { HttpClient, HttpHeaders, HttpErrorResponse, HttpParams } from '@angular/common/http';
import { AllBranchTable , StoreOfBranchTable} from 'app/core/models/branch-manager.model'
import { formatDate } from '@angular/common';
import { data } from 'jquery';
import { CheckNullOrUndefinedOrEmpty } from '../utils/common-function';


@Injectable({
    providedIn: "root",
  })
  export class BranchService {
    constructor(
        private api: ApiService,
        private http: HttpClient,
        ) {}

  getBranchList(): Observable<any>{
    return this.api.get(GetAllBranchApi).pipe(
      map((value) =>
      {
          if (value.code === 200)
          {
              // console.log(value.data);
            return this.renderDataBranchManager(value.data)
          }
      }), catchError(value => throwError(value))
    );
  }

  getBranchListMultiselect(): Observable<any>{
    return this.api.get(GetListBranch).pipe(
      map((value) =>
      {
        if (value.code === 200) {
          let listBranch = [];
          value.data.forEach(branch => {
            listBranch.push({ label: branch.branch_name, value: branch.id });
          });
          return listBranch;
        }
      }), catchError(value => throwError(value))
    );
  }

  createBranch(formBrach){
    return this.api.post(CreateBranchApi , formBrach);
  }

  getStoreOfBranchByUuid(uuid): Observable<any>{
    let param = new HttpParams();
    if(uuid !== null && uuid !== undefined){
      param = param.append('uuid', uuid);
    }
    if(this.api.isEnable()){
      return this.http.get<any>(CreateBranchApi, { headers: this.api.headers, params: param }).pipe(
        map((value) =>
        {
            // console.log(value)
            if (value.code === 200)
            {
              let brandName = (!CheckNullOrUndefinedOrEmpty(value.data)) ? value.data.brand_name : '';
              let listStores = (!CheckNullOrUndefinedOrEmpty(value.data)) ? this.renderDataStoreOfBranch(value.data.store) : [];
              return {branchName : brandName , listStoreTable : listStores }
            }
        }), catchError(value => throwError(value))
      );

    }
  }

  getBranchByUuid(uuid): Observable<any>{
    let param = new HttpParams();
    if(uuid !== null && uuid !== undefined){
      param = param.append('uuid', uuid);
    }
    if(this.api.isEnable()){
      return this.http.get<any>(GetBranchByUuid, { headers: this.api.headers, params: param }).pipe(
        map((value) =>
        {
            if (value.code === 200)
            {
              return value.data;
            }
        }), catchError(value => throwError(value))
      );

    }
  }

  checkBranchAdminExists(email){
    let param = new HttpParams();
    if(email !== null && email !== undefined){
      param = param.append('email', email);
    }
    if(this.api.isEnable()){
      return this.http.get<any>(CheckBranchAdminEmailExists, { headers: this.api.headers, params: param }).pipe(
        map((value) =>
        {
            return value
        }), catchError(value => throwError(value))
      );

    }
  }

  deleteBranch(uuid): Observable<any>{
    let param = {
      uuid: uuid,
    };
    return this.api.put(CreateBranchApi , param)
  }


  updateBranch(uuid , formUpdateBranch): Observable<any>{
    let param = new HttpParams();
    if(uuid !== null && uuid !== undefined){
      param = param.append('uuid', uuid);
    }
    if(this.api.isEnable()){
      return this.http.put<any>(UpdateBranchByUuid, formUpdateBranch, { headers: this.api.headers, params: param });

    }
  }

  renderDataStoreOfBranch(data){
    let listBranchManager = [];
    if(data !== null && data !== undefined && data.length > 0){

      data.forEach(element => {
        let store = new StoreOfBranchTable();
        store.uuid = element.uuid;
        store.storeId = element.id;
        store.storeName = element.store_name;
        store.dateJoin = formatDate(element.created_at, "dd/MM/yyyy HH:mm", "en-US");
        store.isActive = element.is_active;
        if(element.storeAddress !== null && element.storeAddress !== undefined){
          store.address = element.storeAddress.district
        }
        listBranchManager.push(store);
      });
    }
    return listBranchManager;
  }

  renderDataBranchManager(data){
    if(data !== null && data !== undefined && data.length > 0){
      let listStore = [];
      data.forEach(element => {
        let branch = new AllBranchTable();
        branch.uuid = element.uuid;
        branch.branchId = element.id;
        branch.branchName = element.brand_name;
        branch.isActive = element.is_active;
        if(element.managerBy !== null && element.managerBy !== undefined){
          branch.managerId = element.managerBy.id;
          branch.managerName = element.managerBy.first_name + " " +  element.managerBy.last_name;
          branch.phoneNumber = element.managerBy.phone_number;
        }
        branch.dateJoin = formatDate(element.created_at, "dd/MM/yyyy HH:mm", "en-US");
        listStore.push(branch);
      });
      return listStore;
    }
  }


  getPreSignedUrl(fileName: string, fileType: string) {
    const bodyObj = { name: fileName, type: fileType };
    return this.api.post(GetPresignUrlApi, bodyObj).pipe(retry(3), catchError(this.errorHandler));

  }
  uploadBrandImage(url: string, contentType: string, file) {
    const headers = new HttpHeaders({ 'Content-Type': contentType });
    return this.http.put<any>(url, file, { headers: headers, reportProgress: true }).pipe(retry(3), catchError(this.errorHandler));
  }



  private errorHandler(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      console.error('An error occured:', error.error.message);
    }
    else {
      console.error(
        `Back-end return code: ${error.status}\n` +
        `Body content: ${error.status}`
      );
    }

    return throwError(error.message || 'Server Error');
  }
}
