import { NgModule } from "@angular/core";
import { RouterModule } from "@angular/router";
import { FuseSharedModule } from "@fuse/shared.module";
import { MaterialModule } from "../../angular-material/material.module";
import { DropdownModule } from "primeng/dropdown";
import { CalendarModule } from "primeng/calendar";
import { AgGridModule } from "@ag-grid-community/angular";

import { StoreComponent } from "./store.component";
import { StoreDetailComponent } from './store-detail/store-detail.component';
import { CreateStoreComponent } from './create-store/create-store.component'

import { MultiSelectModule } from 'primeng/multiselect';
import { ProgressComponent } from 'app/main/_shared/progress/progress.component';
import { TimeLineModule } from 'app/main/_shared/timeline/timeline.module';
import { StoreOfBranchComponent } from './store-of-branch/store-of-branch.component';
import { RenderEditDeleteComponent } from './render-edit-delete/render-edit-delete.component';

const routes = [
  {
    path: "bk-point/stores",
    component: StoreComponent,
  },
  {
    path: "bk-point/stores/detail",
    component: StoreDetailComponent,
  },
  {
    path: "bk-point/stores/create",
    component: CreateStoreComponent,
  },
  {
    path: "bk-point/stores/store-of-branch",
    component: StoreOfBranchComponent,
  }
];

@NgModule({
  declarations: [
    StoreComponent,
    StoreDetailComponent,
    CreateStoreComponent,
    ProgressComponent,
    StoreOfBranchComponent,
    RenderEditDeleteComponent
  ],
  entryComponents:[RenderEditDeleteComponent],
  imports: [
    RouterModule.forChild(routes),
    FuseSharedModule,
    MaterialModule,
    DropdownModule,
    CalendarModule,
    AgGridModule.withComponents([]),
    MultiSelectModule,
    TimeLineModule
  ],
  exports: [StoreComponent],
})
export class StoresModule {}
