import { Injectable } from "@angular/core";
import { ApiService } from "./api.service";
import { HttpClient, HttpErrorResponse, HttpHeaders } from "@angular/common/http";
import { CommonGetPresignApi} from "./backend-api";
import { Observable, throwError } from "rxjs";
import { catchError, map, retry } from "rxjs/operators";
import { isNullOrUndefined } from "util";
import { formatDate } from "@angular/common";
import { ImageAndEmail, Profile } from "../models/profile.model";
import * as _ from 'lodash';

@Injectable({
  providedIn: "root",
})
export class CommonService {
  constructor(private api: ApiService, private http: HttpClient) {}
    
  getPreSignedUrl(fileName: string, fileType: string)
  {
      const bodyObj = { name: fileName, type: fileType };
      return this.api.post(CommonGetPresignApi, bodyObj).pipe(retry(3), catchError(this.errorHandler));
  }

  private errorHandler(error: HttpErrorResponse)
    {
        if (error.error instanceof ErrorEvent)
        {
            console.error('An error occured:', error.error.message);
        }
        else
        {
            console.error(
                `Back-end return code: ${error.status}\n` +
                `Body content: ${error.status}`
            );
        }

        return throwError(error.message || 'Server Error');
    }

    uploadImage(url: string, contentType: string, file)
    {
        const headers = new HttpHeaders({ 'Content-Type': contentType });
        return this.http.put<any>(url, file, { headers: headers, reportProgress: true }).pipe(retry(3), catchError(this.errorHandler));
    }
  
}
