import { NgModule } from "@angular/core";
import { RouterModule } from "@angular/router";
import { FuseSharedModule } from "@fuse/shared.module";
import { MaterialModule } from "../../angular-material/material.module";
import { DropdownModule } from "primeng/dropdown";
import { CalendarModule } from "primeng/calendar";
import { AgGridModule } from "@ag-grid-community/angular";
import { StoreOfBranchComponent } from "./store-of-branch.component";
import { TimeLineModule } from 'app/main/_shared/timeline/timeline.module';

const routes = [
  {
    path: "bk-point/store-of-branch",
    component: StoreOfBranchComponent,
  },
];

@NgModule({
  declarations: [StoreOfBranchComponent],
  imports: [
    RouterModule.forChild(routes),
    FuseSharedModule,
    MaterialModule,
    DropdownModule,
    CalendarModule,
    AgGridModule.withComponents([]),
    TimeLineModule
  ],
  exports: [StoreOfBranchComponent],
})
export class StoreOfBranchModule {}
