import { Injectable } from '@angular/core';
import { ApiService } from './api.service';
import { Observable, throwError } from 'rxjs';
import { catchError, map, retry } from 'rxjs/operators';
import { GetAllCustomerApi , GetCustomerByUuidApi } from './backend-api';
import { HttpClient, HttpHeaders, HttpErrorResponse, HttpParams } from '@angular/common/http';
import { formatDate } from '@angular/common';
import { CustomerTable , CustomerDetail , CustomerPoint} from 'app/core/models/customer-bkpoint.model'
import { isNullOrUndefined } from 'util';
import { CheckNullOrUndefinedOrEmpty } from '../utils/common-function';

@Injectable({
    providedIn: "root",
  })
  export class CustomerService {
    constructor(
        private api: ApiService,
        private http: HttpClient,
        ) {}

  getCustomerList(): Observable<any>{
    return this.api.get(GetAllCustomerApi).pipe(
      map((value) =>
      {
          if (value.code === 200)
          {
              // console.log(value.data);
              return this.renderDataCustomerTable(value.data)

          }
      }), catchError(value => throwError(value))
    );
  }

  getCustomerByUuid(uuid): Observable<any>{
    let param = new HttpParams();
    if (uuid !== null && uuid !== undefined) {
      param = param.append('uuid', uuid);
    }
    if (this.api.isEnable()){
      return this.http.get<any>(GetCustomerByUuidApi, { headers: this.api.headers, params: param }).pipe(
        map((value) =>
        {
            if (value.code === 200)
            {
                console.log(value.data);
                return this.renderDataCustomerDetail(value.data)

            }
        }), catchError(value => throwError(value))
      );

    }
  }

  renderDataCustomerTable(data){
    if(data !== null && data !== undefined && data.length > 0){
      let listCustomer = [];
      data.forEach(element => {
        let customer = new CustomerTable;
        customer.id = element.id;
        customer.uuid = element.uuid;
        customer.email = element.email;
        customer.phoneNumber = element.phone_number;
        customer.name = element.first_name + " " + element.last_name;
        if(element.address !== null && element.address !== undefined){
          customer.location = element.address.district
        }
        listCustomer.push(customer)
      });
      return listCustomer
    }
  }

  renderDataCustomerDetail(data){
    if(data !== null && data !== undefined){
      let customerDetail = new CustomerDetail();
      customerDetail.name = data.first_name + " " + data.last_name;
      customerDetail.nickname = data.first_name + " " + data.last_name;
      customerDetail.email = data.email;
      customerDetail.birthday = data.day_of_birth;
      customerDetail.phoneNumber = data.phone_number;
      if(data.address !== null && data.address !== undefined){
        customerDetail.address = data.address.detail_address + " "+ data.address.district + " " + data.address.city;
      }
      if(!CheckNullOrUndefinedOrEmpty(data.point)){
        data.point.forEach(element => {
          let point = new CustomerPoint();
          point.id = element.brand_id;
          point.point = element.balance;
          if(element.branchPoint !== null && element.branchPoint !== undefined){
            point.name = element.branchPoint.brand_name
          }
          customerDetail.customerPoint.push(point)
        });

      }
      return customerDetail
    }
  }

}
